.gallery {
  @media screen and (max-width: 768px) {
    padding-bottom: 80px;
    background-image: linear-gradient(to bottom, #84a178 32px, transparent 0);
  }
  @media screen and (min-width: 769px) and (max-width: 1199px) {
    padding-bottom: 100px;
    background-image: linear-gradient(to bottom, #84a178 144px, transparent 0);
  }
  @media screen and (min-width: 1200px) {
    padding-bottom: 140px;
    background-image: linear-gradient(to bottom, #84a178 98px, transparent 0);
  }
}

.gallery__list {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  padding-top: 0;
  padding-bottom: 0;
  background-color: transparent;
  transition: transform $timing-function;
  
    &:hover {
      transform: scale(1.13);
    }

  @media screen and (max-width: 319px) {
    width: 100%;
    height: 130px;
  }
  @media screen and (min-width: 320px) and (max-width: 768px) {
    width: 280px;
    height: 130px;
  }

  @media screen and (min-width: 769px) and (max-width: 1199px) {
    width: 704px;
    height: 327px;
  }
  @media screen and (min-width: 1200px) {
    width: 1013px;
    height: 422px;
  }
}

.gallery__item {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: cf4FadeInOut 9000ms infinite;
  animation: cf4FadeInOut 9000ms infinite;
  // width: 100%;
  // height: 100%;
  display: block;
  overflow: hidden;

  @media screen and (max-width: 768px) {
    border-radius: 8px;
  }

  @media screen and (min-width: 769px) and (max-width: 1199px) {
    border-radius: 18px;
  }
  @media screen and (min-width: 1200px) {
    border-radius: 24px;
  }
}

.gallery__item:nth-child(1) {
  animation-delay: 6s;
  -webkit-animation-delay: 6s;
}

.gallery__item:nth-child(2) {
  animation-delay: 3s;
  -webkit-animation-delay: 3s;
}

.gallery__item:nth-child(3) {
  animation-delay: 0;
  -webkit-animation-delay: 0;
}
